* {
  box-sizing: border-box;
}
.bg {
  background: url(../assets/group_137.png) no-repeat center/cover;
  width: 100vw;
  height: 50vw;
  margin: auto;

}


.name {
  margin-left: 9vw;
  text-align: left;
  color: var(--color_1);
  margin-top: auto;
}
.row_1 {
  padding-top: 1.5vw;
  display: flex;
  /*justify-content: center;
   align-items: center; */
}
.col_0 {
  display: none;
}
.col_1 {
  margin: auto;
  width: 25%;
  display: inline-block;
}
.col_2 {
  margin: auto;
  width: 50%;
  display: inline-block;
}
.col_3 {
  margin: auto;
  width: 25%;
  display: inline-block;
}
.row_2 {
  display: flex;
  flex-flow: row wrap;
}
.col_4 {
  margin: auto;
  width: 50%;
  display: inline-block;
}
.col_44 {
  display: none;
}
.col_5 {
  margin: auto;
  width: 50%;
  display: inline-block;

}

.menu {
  color: var(--color_1);
  padding-left: 13.5%;
  padding-right: 23%;
  text-align: left;
  margin: auto;
  display: flex;
  color: var(--color_1);
}
.menu_item {
  color: var(--color_1);
  display: inline-block;
  margin: auto;
  cursor: pointer;
}
.btn_col {
  text-align: left;
}
.button1 {
  position: relative;
  width: 9.27vw;
  height: 2.3vw;
  background: transparent linear-gradient(270deg, #251854 0%, #594695 100%) 0% 0% no-repeat padding-box;
  border-radius: 25px;
  color: var(--color_1);
  border: 1px solid var(--color_1);
  border-radius: 10px;
  cursor: pointer;
}
.button2 {
  width: 9.27vw;
  height: 2.3vw;
  background-color: var(--color_1);
  border-radius: 25px;
  color: #4F3C88;
  border: 1px solid var(--color_1);
  border-radius: 10px;
  cursor: pointer;
}


a {
  color: var(--color_1);
  text-decoration: none;
}

.img {
  height: 24.53vw;
  width: 24.42vw;
  margin-top: 10.1vw;
  background: url(../assets/logo.png) no-repeat center/cover;
  display: inline-block;
}


.advance {
  margin-top: 10.26vw;
  margin-left: 19.6vw;
  color: var(--color_1);
  text-align: left;
}
.know {
  margin-top: 0.4vw;
  margin-left: 19.6vw;
  width: 25.5vw;
  text-align: left;
  color: var(--color_1);
}

.the {
  margin-top: 1vw;
  margin-left: 19.6vw;
  width: 25.5vw;
  text-align: left;
  color: var(--color_1);
  width: 25.52vw;
}
.b2 {
  margin-left: 19.6vw;
  margin-top: 1.67vw;
  text-align: left;
}

