.row{display: flex;}
.c1{
    display:inline-block;
    width: 13.593%;
    position:fixed;
    overflow-y:auto;
    

   
}
.footer{
    width:86.5%;
    margin-left:13.593%;
}
.c1_open{
    display:inline-block;
    width: 13.593%;
    position:fixed;
    overflow-y:auto;
    
}
.c2{display:inline-block;
    width: 86.407%; 
    margin-left:13.593%;
}

@media only screen and (max-width: 390px) {
    .c1{
        display:none;
        position: static;

    }
    .c2{
        width: 100%;
        margin-left: 0; 
       

    }

    .c1_open{
        display:inline-block;
        width:100%;
        position:fixed;
        top:0;
        left:100;
        background-color: rgb(255,255,255,0.8);
        z-index: 10;


    }
    .footer{
        width:100%;
        margin-left:0vw;
    }

}

@media only screen and (max-width: 768px) and (min-width:391px){
    .footer{
        width:100%;
        margin-left:0vw;
    }

   .c1{
       width:100%;
       position: static;
   }
   .c1_open{
    width:100%;
    position: static; 
   }
   .c2{
       width:100%;
       margin-left: 0;
   }
   .row{
       flex-flow:row wrap;
   }
}