.bg {
  background-color: var(--color_26);
  color: var(--color_1);
  height: 14.62vw;
  overflow-x:hidden;
}

.row {
  display: flex;
}
.c1 {
  display: inline-block;
  width: 13%;
}
.c2 {
  display: inline-block;
  width: 17%;
}
.c3 {
  display: inline-block;
  width: 13%;
}
