  display: inline-block;
  width: 32%;
}
.c5{
width: 25%;
display: inline-block;
}

.col_2 {
  text-align: left;
  padding-right: 6%;
  margin-top: 3.67vw;
}
.col_3 {
  margin-top: 3.07vw;
  text-align: left;
}
.col_4 {
  margin-top: 1.51vw;
  text-align: left;
}

.col_5 {
  margin-top: 2.98vw;

  text-align: left;
}
.col_6 {
  margin-top: 1.22vw;
