}
.col_7 {
  margin-top: 0.35vw;
  text-align: left;
}
.col_8 {
  margin-top:4vw;
  text-align: left;
  
}
.button {
  border: 1px solid var(--color_1);
  width: 30%;
  height: 100%;
  /*background-image: url(../assets/Rectangle_17.png) 0% 0% no-repeat padding-box;*/
  background: transparent linear-gradient(270deg, #4729A5 0%, #4F3F83 100%) 0% 0% no-repeat padding-box;
   
  border-radius: 1.31vw;
  margin-left: -22%;
  color: var(--color_1);
  overflow: hidden;
}
.input {
  width: 70%;
  height: 100%;
  border: 1px solid var(--color_27);
  border-radius: 1.31vw;
  padding-right: 30px;
  padding-left: 20px;
  color: var(--color_28);
}

