.hide{
    display:none;
}
.body{

    position:absolute;
    z-index:100;
    background-color: white;
    
}
.header{
width:18.021vw;
color:#737679;
font-size:1.458vw;
    font-weight:400;
margin-bottom: 1.354vw;
text-align: left;
   
}
.body{
width:18.021vw;
border-left-color:#7DD093;
border-left-style: solid;
border-left-width: 7px;
 box-shadow: 0px 0px 20px #00000012;
}
.Item{
    height:2.968vw;
    color:#737679;
    line-height:2.968vw ;
    font-size:1.0417vw;
    font-weight:400;
    margin:auto;
    vertical-align: middle;
    display: flex;
    

}
.hr{
    border-top: 0.5px solid  var(--color_39);
    width:14.479vw;
    margin-top:0px;
    margin-bottom: 0px;
}
.icon{
    width:1.465vw;
    height: 1.465vw;
    margin-left: 2.135vw;
    visibility: hidden;
    margin-top:0.8vw;
    
}
.c1{
    display: inline-block;
    width:20%;
    visibility: hidden;
  
}
.c2{
    display: inline-block;
    width:80%;
    margin-left: 2.259vw;
    text-align: left;
}
.Item:hover .icon{visibility: visible; }

@media only screen and (max-width: 390px) {

    .header{
        width:60.817vw;
        font-size:5.179vw;
        margin-bottom: 2vw;
        margin-left:auto;
margin-right: auto;
            
        }
        .body{
            width:60.817vw;
            margin-left:auto;
margin-right: auto;
left: 0;
right: 0;
text-align: center;
            }
            .Item{
                height:11.615vw;
                line-height:11.615vw ;
                font-size:4.128vw;
        
            }
            .hr{
                width:50vw;
            }

            .icon{
                width:6.212vw;
                height: 6.212vw;
                margin-left: 5vw;
               
                margin-top:2.1vw;
                
            }
            .c2{
                
                margin-left: 8vw;
                
            }
            .drop{
                text-align: center;
            align-content: center;
            margin:auto
            }

}

@media only screen and (max-width: 768px) and (min-width:391px){
    .header{
        width:30.052vw;
        
        font-size:2.645vw;
        margin-bottom: 1.354vw;   
        }
        .body{
            width:30.052vw;
         
            }
            .Item{
                height:7.421vw;
                line-height:7.421vw ;
                font-size:2.604vw;
            }

            .hr{
                width:24.479vw;
            
            }
            .icon{
                width:3.5vw;
                height: 3.5vw;
                margin-left: 2.135vw;
                margin-top:1.8vw;
                
            }
            .c2{
                display: inline-block;
                width:80%;
                margin-left: 5vw;
                text-align: left;
            }
            .drop{
                margin-left:2vw;
                margin-top:2vw;
            }

}


