.swiper{
    width:100%;;
    height: 24vw;

}
.logo{
    width:2vw;
    height: 2vw;
}
.arrows{
    margin-bottom: 2vw;
    color:#A7A7A7;
    font-size: 2vw;
}
.left{
    margin-right: 1.5vw;
    cursor: pointer;
}
.right{
    margin-left:1.5vw;
    cursor: pointer;
}

@media only screen and (max-width: 390px) {
    .swiper{
        width:100%;;
        height: 95vw;;
     

    
    }
  
    .logo{
        width:6vw;
        height: 6vw;
    }
    .arrows{
        margin-bottom: 2vw;
        color:#A7A7A7;
        font-size: 6vw;
    }
    .left{
        margin-right: 3vw;
        cursor: pointer;
    }
    .right{
        margin-left:3vw;
        cursor: pointer;
    }
    
      
}

@media only screen and (max-width: 768px) and (min-width:391px){
    .swiper{
        width:100%;;
        height: 59.244vw;
    
    }
    .logo{
        width:5vw;
        height: 5vw;
    }
    .arrows{
        margin-bottom: 4vw;
        font-size: 5vw;
    }
    .left{
        margin-right: 3vw;  
    }
    .right{
        margin-left:3vw;
    }
}