.menu{
text-align: left;}
.btn{
    width:2.640vw;
    height:1.562vw;
    font-weight: 700;
    font-size:0.625vw;
    margin-right:0.208vw;
    border:none;
    border-radius: 0.520vw;
    margin-top:1.041vw;
    margin-bottom:1.3vw;
    color:#00000083
  }
.App{
    margin-right:3vw;
    margin-bottom: 5vw;
}
.gray{
  background-color:#E8E8E8;
}
.orange{
  background-color: #7DD093
}
@media only screen and (max-width: 390px) {
  .btn{
    width:10.82vw;
    height:5.692vw;
    font-size:3.076vw;
    margin-left:1.025vw;
    margin-right: 0vw;
    border-radius: 2vw;
    margin-top:5.128vw;
    margin-bottom:2.528vw;
   
  }
  .App{
  
    margin-bottom: 10vw;
}
}
@media only screen and (max-width: 768px) and (min-width:391px){
  .App{
  
    margin-bottom: 7vw;
}
  .btn{
    width:6.510vw;
    height:3.906vw;
    font-size:1.562vw;
    margin-right: 0vw;
    margin-left:2.52px;
    border-radius: 1.302vw;
    margin-top:2.604vw;
    margin-bottom:1.302vw;
  
  }
}
