.dash{
    overflow-x: hidden;
}
.dashbooard{
    font-family: var(--font_5);
    margin-top:4.479vw;
    text-align: left;
    color:var(--color_37);
    margin-left:3.645vw;
}
.choose{
    margin-left:3.645vw;   
}
.menu_item1{
    margin-top:1.442vw; 
    display: flex;
    vertical-align: middle;
    justify-content: center;
    margin-bottom: 1vw;
}
.hide{
    display:none;
}
.icon1{
    height: 2.604vw;
    width: 2.604vw;
    margin-right:0.692vw;
}
.menu_text1{
    color:var(--color_43);
    vertical-align: middle;
    margin-top:auto;
    margin-bottom: auto;

}

.menu_item2{
    margin-top:1.442vw;
    text-align: right;

    display: flex;
   margin-left:2vw;
    vertical-align: middle;
    font-family: var(--font_1);
   

}
.menu_item3{
    margin-top:1.12vw;
    text-align: right;
    display: flex;
   margin-left:2vw;
    vertical-align: middle;
    font-family: var(--font_1); 
}
.icon2{
    height: 2.082vw;
    width: 2.082vw;
    margin-right:0.692vw;
}
.menu_text2{
    color:var(--color_43);
    vertical-align: middle;
    margin-top:auto;
    margin-bottom: auto;
    font-family: var(--font_1);

}

.hr{
    border-top: 1px solid #D4D6DD9A;
    width:83.26vw;
 
    margin-top:1.2vw;
}
.row{display: flex;
}
.c1{
    display:inline-block;
    width: 15%;
}
.c2{display:inline-block;
    width: 25%; 
    
}
.c3{
    display:inline-block;
    width: 60%;
}
.menuImg{
    display:none;
}
.select{
    width:14.481vw;
    height:2.5vw;
    background-color: white;
    color:#737679;
    border:none;
    scrollbar-color: yellow blue;
}
.option{
    height:2.5vw;
    background-color: white;
    color:#737679;

}
.csspan{
    background-color: #EDEDED;
    color:#6C6C6C;
    width:16.375vw ;
    
    text-align: left;
    height:2.239vw;
    border-radius: 0.729vw;
    font-size:1.198vw;
      font-weight:400 ;
      font-family: var(--font_2);
      line-height:2.239vw; ;
      display: inline-block;
    
    }
    .caButton{
        background-color: #7DD093;
        color:white;
        width:8.334vw;
        height:2.239vw;
        border:none;
        border-radius: 0.729vw;
        font-size:1.198vw;
        font-weight:400 ;
        font-family: var(--font_2);  
      }
@media only screen and (max-width: 390px) {
    .caButton{
   
        width:30.025vw;
        height:9.025vw;
        border-radius: 3.59vw;
        font-size:4vw;
      
      }
      .csspan{
 
        width:54.461vw ;
        height:9.025vw;
        border-radius: 3.59vw;
        font-size:4vw; 
          line-height:9.025vw; 
        }
    
    .c3{
        display: none;
    }
    .c1{
        width:40%;
    }
    .c2{
        width:60%;
        margin-bottom: 5vw;;
    }

    .dashbooard_s{
        margin-top:8.205vw;
        text-align: center;
    }
    .menuImg{
display: inline-block;
position:absolute;
top:5.128vw;
left:2.307vw;
color:var(--color_51);
font-size: 6vw;;
    }

    .icon1{
        height: 10.83vw;
        width: 10.83vw;
        margin-right:2.866vw;
        margin-left:8vw;
    }

    .icon2{
        height: 7.72vw;
        width: 7.72vw;
        margin-right:2.866vw;
    }

    .menu_text2{
       padding-right:15vw;
    
    }
    .hr{
        visibility: hidden;
    }
    

}

@media only screen and (max-width: 768px) and (min-width:391px){
    .csspan{
        width:35.76vw ;
        height:5.6vw;
        border-radius:1.823vw;
        font-size:2.5vw;  
          line-height:5.6vw; ;
       
        }
        .caButton{
          width:18.84vw;
          height:5.6vw;
          border-radius: 1.823vw;
          font-size:2.5vw; 
          
        }
  
    .dashbooard{
        display: none;
    }
    .hr{
        display:none;
    }

    .row{
        justify-content: center;
    }

    .menu_item1{
        margin-top:3.214vw;
        margin-left:auto;
        
        }
    .icon1{
        height: 4.427vw;
        width: 4.427vw;
        margin-right:1.190vw;
    }
    
    .menu_item2{
justify-content: flex-end;    
        margin-left:4.855vw;
        margin-top:3.214vw;

    }
    .icon2{
        height: 3.802vw;
        width: 3.802vw;
        margin-right:1.876vw;
    }
    .c2{
        width:40%;
    }
    .c1{
        width:40%;   
    }
    .c3{
        width:20%;
    }
}
