div{
    text-align:center;
}
.header_span{

    text-align: center;
    letter-spacing: 0px;
    opacity: 1;

    color:var(--color_7)
}
.header_div{
    margin-bottom:1.197vw;
    margin-top:13.56vw;
}

.mail_span{

    text-align: left;
    letter-spacing: 0px;
    opacity: 1;
    color:var(--color_30)
    
}
.mail_div{
    margin-bottom:3.0729vw;
}

@media only screen and (max-width: 390px) {
   
    .header_div_s{
        margin-bottom:3.589vw !important;
        margin-top:22.05vw !important;
    }
    .mail_div_s{
        margin-bottom:16.67vw !important;
    }

}

@media only screen and (max-width: 768px) and (min-width:391px){

    .header_div{
        margin-bottom:1.953vw !important;
        margin-top:17.057vw !important;
    }
    
    .mail_div{
        margin-bottom:4.427vw;
    }

}


